.page__container {
  width: 320px;
  margin: 0 auto;
  font-family: "ptsans", sans-serif;
  background-color: #ffffff;

  @media (min-width: $tablet-width) {
    width: $tablet-width;
    margin: 0 auto;
  }

  @media (min-width: $desktop-width) {
    width: $desktop-width;
    margin: 0 auto;
    box-shadow: 0px 5px 15px rgba(0, 1, 1, 0.2);
  }
}

.page__header {
}

