}

.page__adventages {
  margin-bottom: 35px;
  @media (min-width: $tablet-width) {
    margin-bottom: 60px;
  }

  @media (min-width: $desktop-width) {
    width: $desktop-width;
    margin-bottom: 60px;
  }
}

